.grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 24px;
  color: $text-white;
  padding: 60px 0 92px 0;
  :first-child {
    padding-bottom: 32px;
  }

  @include onTablet {
    padding: 120px 0 132px 0;
  }

  @include onDesktop {
    padding: 120px 0 164px 0;
  }



@include onTablet {
  grid-template-columns: repeat(6, 1fr);
  column-gap: 24px;
}

@include onDesktop {
  grid-template-columns: repeat(12, 1fr);
  column-gap: 24px;
}

&__item-1-4 {
  grid-column: 1 / 2;
  font-size: 32px;
  line-height: 32px;
  letter-spacing: -1.5px;
  text-align: left;
  color: $text-white;

  @include onTablet {
    grid-column: 1 / 4;
  }

  @include onDesktop {
    grid-column: 1 / 5;
  }
}

&__item--title {
  grid-column: 1 / 3 ;
  margin: 0;
  font-size: 32px;
  font-weight: 500;
  width: 100%;

  @include onTablet {
    grid-column: 1 / 3;
    font-size: 48px;
    line-height: 48px;
  }

  @include onDesktop {
    grid-column: 1 / 4;
    font-size: 64px;
    line-height: 64px;
  }
}

&__item--paragraf {
  margin: 0;
}


&__item-4-6 {
  grid-column: 1 / 2;
  font-size: 23px;
  line-height: 32px;
  margin: 0;
  color: $text-white;

  @include onTablet {
    grid-column: 4 / 6;
  }

  @include onDesktop {
    grid-column: 6 / 11;
  }
}
}

.container {
margin: 0 auto;
padding: 0 20px;
}
